import React from 'react';
import { useNavigate, Navigate } from 'react-router-dom';
import { useForm } from 'react-hook-form';
import shallow from 'zustand/shallow';
import useStore from '../../store';

const Register = () => {
  const navigate = useNavigate();
  const { onRegister, registerSuccess } = useStore((state) => {
    return {
      onRegister: state.onRegister,
      registerSuccess: state.registerSuccess,
    };
  }, shallow);
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const atSubmit = (data) => {
    onRegister(data);
  };
  if (registerSuccess) {
    return <Navigate to="/user/login" />;
  }
  return (
    <form
      onSubmit={handleSubmit(atSubmit)}
      className="container m-auto flex max-h-[500px] max-w-lg flex-col overflow-y-scroll rounded-xl border-8 shadow-xl backdrop-blur-lg md:h-[55%]"
    >
      <h2 className="w-full p-2 text-center"></h2>
      <label className="flex h-[33%] flex-col justify-center px-2">
        
        <input
          className="mt-2 rounded-sm p-2 leading-5"
          type="text"
          placeholder=""
          {...register('name', { maxLength: 7 })}
        />
        {errors.name && <small className="text-danger">7</small>}
      </label>

      <label className="flex h-[33%] flex-col justify-center px-2">
        
        <input
          className="mt-2 rounded-sm p-2 leading-5"
          type="text"
          placeholder=" email"
          {...register('email', { required: true })}
        />
        {errors.email?.type === 'required' && (
          <small className="text-danger"></small>
        )}
      </label>

      <label className="flex h-[33%] flex-col justify-center px-2">
        
        <input
          className="mt-2 rounded-sm p-2 leading-5"
          type="text"
          placeholder=""
          {...register('password', { required: true, maxLength: 12 })}
        />
        {errors.password?.type === 'required' && (
          <small className="text-danger"></small>
        )}
        {errors.password?.type === 'maxLength' && (
          <small className="text-danger">12</small>
        )}
      </label>
      <label className="flex h-[33%] flex-col justify-center px-2">
        
        <input
          className="mt-2 rounded-sm p-2 leading-5"
          type="text"
          placeholder=""
          {...register('checkPassword', { required: true, maxLength: 12 })}
        />
        {errors.checkPassword?.type === 'required' && (
          <small className="text-danger"></small>
        )}
        {errors.checkPassword?.type === 'maxLength' && (
          <small className="text-danger">12</small>
        )}
      </label>
      <div className="my-auto flex flex-col justify-evenly py-2 px-2 md:flex-row md:px-0">
        <input
          type="submit"
          className="my-1 w-full rounded-md  bg-primary p-2  text-white md:w-[30%]"
        />
        <button
          onClick={() => navigate(-1)}
          className="my-1 w-full rounded-md  bg-light p-2 text-center text-white md:w-[30%]"
        >
          
        </button>
      </div>
    </form>
  );
};

export default React.memo(Register);
